@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
* {
  box-sizing: border-box;
}

#root {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: Tahoma, sans-serif;
  background:white;

}

.App {
  height: 100%;
  display: flex;
  color: rgba(189, 189, 192, 1);
  font-weight: 400;
  font-family: "Nunito Sans", sans-serif;
}

.User {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  transition:0.3s;
  -webkit-transition:0.3s;
  -moz-transition:0.3s;
  -ms-transition:0.3s;
  -o-transition:0.3s;
  background:#04817529;
  border-top:solid 1px white;
  
}
.User:hover {
 
  cursor: pointer;
  background:white;
}
.User__pic-container{
  position:relative;
}
.User__pic {
  width: 50px;
  border-radius: 50%;
  box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.1);
  transition: 0.3s;
}
.User__status{
  width:10px;
  height:10px;
  background: #8BC34A;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  border: solid 1px white;
  left: 38px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.user:hover > .User__pic{
  box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.1);
}
.User__details-name{
  color:#222;
}
.User__details {
  display: none;
  color:#777;
  font-size: 12px;
}

/* not small devices  */
@media (min-width: 576px) {
  .User__details {
    display: block;
    padding: 0 0 0 1rem;
  }
  .User__details-name {
    margin: 0;
   
    font-size: 1rem;
  }
}

.Sidebar {
  width: 80px;
  background-color: #ffffff;
  height: 100%;
  border-right: 1px solid rgba(189, 189, 192, 0.1);
  overflow-y: scroll;
  transition: width 0.3s;
}
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color:#025d54;
  
}
::-webkit-scrollbar {
  width: 5px;
  background: #eee;
}
/* not small devices  */
@media (min-width: 576px) {
  .Sidebar {
    width: 250px;
  }
}

.Main {
  flex: 1 1;
  background-color: white;
  height: 100%;
}

.Empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background:#025d54;
}
.Empty__name {
  color: #fff;
}
.Empty__status,
.Empty__info {
  padding: 1rem;
}
.Empty__status {
  color: rgba(255, 255, 255, 0.9);
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}
.Empty__img {
  border-radius: 50%;
  margin: 2rem 0;
  border: solid 4px white;
  box-shadow: 0 0 12px 8px #ffffff7d;
  width:200px;
  height:200px;
}
.Empty__btn {
  padding: 1rem;
  margin: 1rem 0;
  font-weight: bold;
  font-size: 1.2rem;
  border-radius: 30px;
  outline: 0;
}
.Empty__btn:hover {
  background: rgba(255, 255, 255, 0.7);
  cursor: pointer;
}

.Header {
  padding: 1rem 2rem;
  display:flex;
  margin-left: -1px;
    z-index: 1;
  border-bottom: 1px solid rgba(189, 189, 192, 0.2);
  background:linear-gradient(to Right,#025d54,#058478)
}
.Header__img{
  padding-right:1rem;
}
.Header__name {
  color: #fff;
  font-size:1rem;
  margin:10px 0 0 0;
}
.Header__status{
  margin:0px;
}

@media (max-width: 576px) {
  .Header__status {
    display:none;
  }
}

.Chats {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 95%;
  margin: 0 auto;
  overflow-y: scroll;
}
.Chat {
  margin: 0.5rem 0;
  color: white;
  padding: 1rem;
  background: #058478;
  max-width: 90%;
  border-radius: 0 10px 10px 10px;
  -webkit-border-radius: 0 10px 10px 10px;
  -moz-border-radius: 0 10px 10px 10px;
  -ms-border-radius: 0 10px 10px 10px;
  -o-border-radius: 0 10px 10px 10px;
}
.Chat.is-user-msg {
  position: relative;
  margin-left: auto;
  margin-right:1rem;
  background: #4CAF50;
  color:white;
  border-radius:10px 10px 0 10px;
  cursor: pointer;
  -webkit-border-radius:10px 10px 0 10px;
  -moz-border-radius:10px 10px 0 10px;
  -ms-border-radius:10px 10px 0 10px;
  -o-border-radius:10px 10px 0 10px;
}
.Chat.is-user-msg:hover .Chat__close {
  display: inline-block;
}
.Chat__close {
  position: absolute;
  top: -5px;
  left: -5px;
  font-size: 0.7rem;
  border: 1px solid #fff;
  border-radius: 50%;
  display: none;
  text-align: center;
  line-height: 1.3rem;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: black;
}

@media (min-width: 576px) {
  .Chat {
    max-width: 60%;
  }
}

.Message {
  width: 90%;
  margin: 1rem auto;
}
.Message__input {
  width: 100%;
  padding: 1rem;
  
  color: #222;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  outline: 0;
  background: #eee;
  border: solid 1px #ddd;
}

.ChatWindow {
    display: flex;
    flex-direction: column;
    height: 100vh;
}
